<template>
    <div class="container">
        <nav-bar title="领券中心" :border="false"></nav-bar>
        <div class="list" v-if="couponList.length>0">
           <coupon-item :coupon="item" :index="index" type="coupon1" v-for="(item, index) in couponList" :key="item.coupon_id" @receiveCoupon="receiveCoupon"></coupon-item>
        </div>
    </div>
</template>

<script>
import CouponItem from '@/components/CouponItem.vue'
export default {
  name: 'CouponIndex',
  components: {
    CouponItem
  },
  data () {
    return {
      couponList: []
    }
  },
  created () {
    this.getCouponList()
  },
  methods: {
    async getCouponList () {
      const { data } = await this.$api.coupon.getCouponList()
      this.couponList = data.list
    },
    async receiveCoupon (id) {
      await this.$api.coupon.receiveCoupon(id)
      this.$toast.success('领取成功')
    }
  }
}
</script>

<style lang="less" scoped>
.container{
    min-height: 100vh;
    background-color: @primaryBg;
}
.list{
    padding: 10px;
}
</style>
